import { useEffect, useRef, useState } from "react";
import {
  Box,
  Button,
  Container,
  Slider,
  Stack,
  TextField,
  Typography,
  Paper,
  IconButton,
  Tooltip,
  LinearProgress,
} from "@mui/material";
import InfoOutlinedIcon from "@mui/icons-material/InfoOutlined";
import VolumeUpIcon from "@mui/icons-material/VolumeUp";

export default function AlarmGamePage() {
  const [minutes, setMinutes] = useState(3);
  const [totalSeconds, setTotalSeconds] = useState(180);
  const [elapsed, setElapsed] = useState(0);

  const [isMinuteFocus, setIsMinuteFocus] = useState(false);
  const [isSecondFocus, setIsSecondFocus] = useState(false);

  const [status, setStatus] = useState("");
  const [volume, setVolume] = useState(0.5);

  const [isRunning, setIsRunning] = useState(false);
  const [isAlarming, setIsAlarming] = useState(false);
  const [isPreviewPlaying, setIsPreviewPlaying] = useState(false);

  const timerRef = useRef<number | null>(null);
  const elapsedRef = useRef<number | null>(null);
  const audioRef = useRef<HTMLAudioElement | null>(null);

  useEffect(() => {
    const audio = new Audio("/alarm.mp3");
    audio.volume = volume;
    audioRef.current = audio;
  }, []);

  useEffect(() => {
    if (!audioRef.current) return;
    audioRef.current.volume = volume;
  }, [volume]);

  useEffect(() => {
    if (!isRunning) return;

    elapsedRef.current = window.setInterval(() => {
      setElapsed((v) => v + 1);
    }, 1000);

    return () => {
      if (elapsedRef.current) clearInterval(elapsedRef.current);
    };
  }, [isRunning]);

  const prepareAudioPermission = () => {
    if (!audioRef.current) return;
    const audio = audioRef.current;
    audio.volume = 0;
    audio.play().then(() => {
      audio.pause();
      audio.currentTime = 0;
      audio.volume = volume;
    });
  };

  const playPreviewSound = () => {
    if (!audioRef.current) return;

    const audio = audioRef.current;

    //      
    if (isAlarming || isPreviewPlaying) {
      audio.pause();
      audio.currentTime = 0;
      setIsAlarming(false);
      setIsPreviewPlaying(false);
      setStatus("");
      return;
    }

    //  
    audio.pause();
    audio.currentTime = 0;
    audio.volume = volume;
    audio.play();
    setIsPreviewPlaying(true);

    audio.onended = () => {
      setIsPreviewPlaying(false);
    };
  };

  const playSound = () => {
    if (!audioRef.current) return;
    const audio = audioRef.current;
    audio.currentTime = 0;
    audio.play();
    setIsPreviewPlaying(false);
  };

  const startGame = () => {
    if (totalSeconds <= 10) {
      alert("10   .");
      return;
    }

    stopAll();
    prepareAudioPermission();

    setElapsed(0);
    setStatus(" ...");
    setIsRunning(true);

    const min = 10;
    const max = totalSeconds;
    const randomTime = Math.floor(Math.random() * (max - min) + min);

    timerRef.current = window.setTimeout(() => {
      setStatus("  !");
      setIsRunning(false);
      setIsAlarming(true);
      playSound();
    }, randomTime * 1000);
  };

  const stopWaiting = () => {
    if (timerRef.current) clearTimeout(timerRef.current);
    if (elapsedRef.current) clearInterval(elapsedRef.current);
    setIsRunning(false);
    setStatus("");
  };

  const stopAlarm = () => {
    if (audioRef.current) {
      audioRef.current.pause();
      audioRef.current.currentTime = 0;
    }
    setIsAlarming(false);
    setIsPreviewPlaying(false);
    setStatus("");
  };

  const stopAll = () => {
    stopWaiting();
    stopAlarm();
  };

  const progress = Math.min((elapsed / totalSeconds) * 100, 100);

  return (
    <Container maxWidth="sm">
      <Paper elevation={4} sx={{ p: 3, mt: 4, borderRadius: 3 }}>
        <Stack spacing={3}>
          <Typography variant="h4" textAlign="center">
             Random Bomb
          </Typography>

          {/* Bomb */}
          <Box
            sx={{
              fontSize: 64,
              textAlign: "center",
              animation: isRunning
                ? "pulse 1s ease-in-out infinite"
                : "none",
              "@keyframes pulse": {
                "0%": { transform: "scale(1)" },
                "50%": { transform: "scale(1.15)" },
                "100%": { transform: "scale(1)" },
              },
            }}
          >
            
          </Box>

          <Typography textAlign="center">{status}</Typography>

          {/* Progress */}
          <Box>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Typography>
                : {elapsed} / {totalSeconds} 
              </Typography>

              <Tooltip
                title="        ."
                enterTouchDelay={0}
              >
                <IconButton size="small">
                  <InfoOutlinedIcon fontSize="small" />
                </IconButton>
              </Tooltip>
            </Stack>

            <LinearProgress
              variant="determinate"
              value={progress}
              sx={{ height: 8, borderRadius: 5, mt: 1 }}
            />
          </Box>

          {/* Minutes */}
          <TextField
            fullWidth
            label=""
            type="number"
            disabled={isRunning || isAlarming}
            value={isSecondFocus ? "" : minutes}
            placeholder={
              isSecondFocus ? String(Math.floor(totalSeconds / 60)) : undefined
            }
            onFocus={() => setIsMinuteFocus(true)}
            onBlur={() => setIsMinuteFocus(false)}
            onChange={(e) => {
              const m = Number(e.target.value);
              setMinutes(m);
              setTotalSeconds(m * 60);
            }}
          />

          {/* Seconds */}
          <TextField
            fullWidth
            label=" "
            type="number"
            disabled={isRunning || isAlarming}
            value={isMinuteFocus ? "" : totalSeconds}
            placeholder={isMinuteFocus ? String(totalSeconds) : undefined}
            onFocus={() => setIsSecondFocus(true)}
            onBlur={() => setIsSecondFocus(false)}
            onChange={(e) => {
              const s = Number(e.target.value);
              setTotalSeconds(s);
              setMinutes(Math.floor(s / 60));
            }}
          />

          {/* Volume */}
          <Box>
            <Stack direction="row" alignItems="center" spacing={1}>
              <Typography></Typography>

              <Tooltip
                title={
                  isAlarming || isPreviewPlaying
                    ? "   ."
                    : "   ."
                }
                enterTouchDelay={0}
              >
                <IconButton onClick={playPreviewSound}>
                  <VolumeUpIcon />
                </IconButton>
              </Tooltip>
            </Stack>

            <Slider
              value={volume}
              min={0}
              max={1}
              step={0.01}
              onChange={(_, v) => setVolume(v as number)}
            />
          </Box>

          {/* Buttons */}
          <Stack direction="row" spacing={2}>
            {!isRunning && !isAlarming && (
              <Button fullWidth variant="contained" onClick={startGame}>
                
              </Button>
            )}

            {isRunning && (
              <Button
                fullWidth
                variant="outlined"
                color="warning"
                onClick={stopWaiting}
              >
                
              </Button>
            )}

            {isAlarming && (
              <Button
                fullWidth
                variant="contained"
                color="error"
                onClick={stopAlarm}
              >
                
              </Button>
            )}
          </Stack>
        </Stack>
      </Paper>
    </Container>
  );
}
